<template>
  <div id="app">
    <h1 class="clock">{{ time }}</h1>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: new Date().toLocaleTimeString(),
      backgroundColor: '#98FB98',
    };
  },
  created() {
    this.updateBackgroundColor();
    setInterval(() => {
      this.time = new Date().toLocaleTimeString();
      this.updateBackgroundColor();
    }, 1000);
  },
  mounted() {
    document.body.style.backgroundColor = this.backgroundColor;
  },
  methods: {
    updateBackgroundColor() {
      const now = new Date();
      const hour = now.getHours();
      const minutes = now.getMinutes();
      const time = hour + minutes / 60;

      if (time < 6 || time >= 20.5) {
        this.backgroundColor = '#FFC1C1'; // Pastel red for sleep time
      } else if ((time >= 6 && time < 7) || (time >= 20 && time < 20.5)) {
        this.backgroundColor = '#FFDA89'; // Pastel orange for transition time
      } else {
        this.backgroundColor = '#98FB98'; // Pastel green for active time
      }
      document.body.style.backgroundColor = this.backgroundColor;
    }
  }
};
</script>

<style scoped>
#app {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  /* background-color: #98FB98; */
}

.clock {
  font-size: 9em;
  font-family: 'Arial Rounded MT', sans-serif;
  text-align: center;
}
</style>
